// @import-normalize;
@import 'helper';
 *{
     margin: 0;
     padding: 0;
 }
 *{
     box-sizing: border-box;
 }
 *::after{
    box-sizing: border-box;
 }
 *::before{
    box-sizing: border-box;
 }
 ul,ol{
list-style: none;
 }
 a{
   text-decoration: none;
   color: inherit;
 }
 body{
  font-family: $font-hei;
  font-size: 16px;
  max-width: 500px;
  margin: 0 auto;
  height: 100vh;
  background: rgb(206, 201, 201);
 }
 .icon{
   display: inline-block;
 }
 button{
   font-family: inherit;
 }
 :focus{
   outline: none;
 }
 .clearfix::after{
   content: '';
   display: block;
   clear: both;
 }